<template>
  <v-app id="my-job-tracker">

    <v-app-bar app>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>

      <v-toolbar-title>The Job Tracker</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-col cols='3'>
        <v-text-field v-if="$auth.isAuthenticated" outlined label="Search" append-icon="mdi-magnify" dense class='mt-5'>
        </v-text-field>
      </v-col>
    </v-app-bar>
    <v-navigation-drawer v-model="drawer" app>

      <v-divider></v-divider>

      <v-list v-if="$auth.isAuthenticated">
        <v-list-item v-for="[icon, text, tabName] in links" :key="icon" link @click='activeTab = [text, tabName]'>
          <v-list-item-icon>
            <v-icon>{{ icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item @click='logout'>
          <v-list-item-icon>
            <v-icon>mdi-logout</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>Logout</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

      </v-list>
      <v-list v-if="!$auth.isAuthenticated">
        <v-list-item @click='login'>
          <v-list-item-icon>
            <v-icon>mdi-login</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>Login</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>


    <v-main>
      <Splash v-if="!$auth.isAuthenticated"></Splash>
      <v-container class="py-8 px-6" fluid v-if="$auth.isAuthenticated">
        <v-row>
          <v-col cols="12">
            <v-card>
              <v-subheader>{{ activeTab[0] }}</v-subheader>
              <v-list two-line>
                <template v-for="job in jobs">
                  <v-list-item :key='job.id'>
                    <v-list-item-avatar :style='{"background-color" : job.color ? job.color : generateColorString()}'>
                      {{job.company.match(/\b\w/g).join('')}}
                    </v-list-item-avatar>

                    <v-list-item-content>
                      <v-list-item-title>{{job.company}}</v-list-item-title>

                      <v-list-item-subtitle>
                        {{job.dateApplied}}
                      </v-list-item-subtitle>
                    </v-list-item-content>
                  </v-list-item>

                  <v-divider :key='job.name'></v-divider>
                </template>
              </v-list>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
  import Splash from './components/Splash.vue'

  export default {
    data: () => ({
      activeTab: ['Active', 'activeJobs'],
      drawer: false,
      links: [
        ['mdi-account-check', 'Active', 'activeJobs'],
        ['mdi-email-send', 'Applied', 'appliedJobs'],
        ['mdi-archive', 'Archived', 'archievedJobs'],
      ],
    }),
    components: {
      Splash
    },
    methods: {
      generateColorString: function () {
        var colorArr = [
          "Black",
          "Blue",
          "BlueViolet",
          "BurlyWood",
          "CadetBlue",
          "Chartreuse",
          "Chocolate",
          "Coral",
          "CornflowerBlue",
          "Crimson",
          "DarkBlue",
          "DarkCyan",
          "DarkGoldenRod",
          "DarkGreen",
          "DarkMagenta",
          "DarkOliveGreen",
          "DarkOrange",
          "DarkOrchid",
          "DarkRed",
          "DarkSalmon",
          "DarkSeaGreen",
          "DarkSlateBlue",
          "DarkSlateGray",
          "DarkSlateGrey",
          "DarkTurquoise",
          "DarkViolet",
          "DeepPink",
          "DeepSkyBlue",
          "DodgerBlue",
          "FireBrick",
          "ForestGreen",
          "Fuchsia",
          "Gold",
          "GoldenRod",
          "Green",
          "GreenYellow",
          "HotPink",
          "IndianRed",
          "Indigo",
          "LawnGreen",
          "Lime",
          "Magenta",
          "Maroon",
          "MediumAquaMarine",
          "MediumBlue",
          "MediumOrchid",
          "MediumPurple",
          "MediumSeaGreen",
          "MediumSlateBlue",
          "MediumSpringGreen",
          "MediumTurquoise",
          "MediumVioletRed",
          "MidnightBlue",
          "Navy",
          "Olive",
          "OliveDrab",
          "Orange",
          "OrangeRed",
          "Orchid",
          "Peru",
          "Pink",
          "Plum",
          "Purple",
          "RebeccaPurple",
          "Red",
          "RosyBrown",
          "RoyalBlue",
          "SaddleBrown",
          "Salmon",
          "SandyBrown",
          "SeaGreen",
          "Sienna",
          "Silver",
          "SlateBlue",
          "SteelBlue",
          "Teal",
          "Tomato",
          "Violet",
        ]
        return colorArr[Math.floor(Math.random() * colorArr.length)];
      },
      // Log the user in
      login() {
        this.$auth.loginWithRedirect();
      },
      // Log the user out
      logout() {
        this.$auth.logout({
          returnTo: window.location.origin
        });
      }
    },
    computed: {
      jobs: function () {
        if (this.$auth.isAuthenticated) {
          if (this.activeTab[1] == 'activeJobs') {
            return this.$store.state.activeJobs;
          } else if (this.activeTab[1] == 'appliedJobs') {
            return this.$store.state.appliedJobs;
          } else {
            return this.$store.state.archivedJobs;
          }
        } else {
          return []
        }
      },
      allJobs: function () {
        var allJobArr = [];
        allJobArr = allJobArr.concat(this.$store.state.activeJobs);
        allJobArr = allJobArr.concat(this.$store.state.appliedJobs);
        allJobArr = allJobArr.concat(this.$store.state.archivedJobs);
        return allJobArr;
      }
    }
  }
</script>